@charset "UTF-8";
* {
    padding: 0;
    margin: 0
}

a {
    text-decoration: none
}

.pagelist {
    clear: both;
    width: 1170px;
    margin: 0 auto;
    text-align: right;
    padding-right: 20px
}

.m-style {
    float: right;
    text-align: center
}

.m-style:after,
.m-style:before {
    content: "";
    display: table
}

.m-style:after {
    clear: both;
    overflow: hidden
}

.m-style span {
    float: left;
    margin: 0 5px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    color: #bdbdbd;
    font-size: 14px
}

.m-style .active {
    float: left;
    margin: 0 5px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    background: #ff9500;
    color: #fff;
    font-size: 14px;
    border: 1px solid #ff9500
}

.m-style a {
    float: left;
    margin: 0 5px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    background: #fff;
    border: 1px solid #ebebeb;
    color: #bdbdbd;
    font-size: 14px
}

.m-style a:hover {
    color: #fff;
    background: #ff9500
}

.m-style .next,
.m-style .prev {
    font-family: Simsun;
    font-size: 16px;
    font-weight: 700;
    width: 80px
}

.count,
.now {
    padding: 0 5px;
    color: red
}

.eg img {
    max-width: 800px;
    min-height: 500px
}

input {
    float: left;
    margin: 0 5px;
    width: 38px;
    height: 38px;
    line-height: 38px;
    text-align: center;
    background: #fff;
    border: 1px solid #ebebeb;
    outline: 0;
    color: #bdbdbd;
    font-size: 14px
}